import React, { useState, useEffect } from "react";
import axios from "axios";
import "./Marketplace.css";

function App() {
  const [foods, setFoods] = useState([]);
  const [cart, setCart] = useState([]);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("All");
  const [newFood, setNewFood] = useState({
    name: "",
    price: "",
    category: "",
    image: "",
  });

  //  Fetch data from TheMealDB API
  useEffect(() => {
    const fetchData = async () => {
      try {
        const categoryRes = await axios.get(
          "https://www.themealdb.com/api/json/v1/1/list.php?c=list"
        );
        setCategories(["All", ...categoryRes.data.meals.map((c) => c.strCategory)]);

        const foodRes = await axios.get(
          "https://www.themealdb.com/api/json/v1/1/search.php?s="
        );
        setFoods(foodRes.data.meals);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    fetchData();
  }, []);

  //  Filter by category
  const handleFilter = (category) => {
    setSelectedCategory(category);
  };

  //  Add to cart
  const addToCart = (food) => {
    const existingItem = cart.find((item) => item.idMeal === food.idMeal);
    if (existingItem) {
      setCart(
        cart.map((item) =>
          item.idMeal === food.idMeal
            ? { ...item, quantity: item.quantity + 1 }
            : item
        )
      );
    } else {
      setCart([...cart, { ...food, quantity: 1 }]);
    }
  };

  //  Remove from cart
  const removeFromCart = (idMeal) => {
    setCart(cart.filter((item) => item.idMeal !== idMeal));
  };

  //  Add custom food manually
  const handleChange = (e) => {
    const { name, value } = e.target;
    setNewFood({ ...newFood, [name]: value });
  };

  const handleAddFood = (e) => {
    e.preventDefault();
    if (!newFood.name || !newFood.price || !newFood.category || !newFood.image)
      return;

    const newItem = {
      idMeal: Date.now().toString(),
      strMeal: newFood.name,
      strMealThumb: newFood.image,
      strCategory: newFood.category,
      price: parseInt(newFood.price, 10),
    };

    setFoods([...foods, newItem]);
    setNewFood({ name: "", price: "", category: "", image: "" });
  };

  //  Apply category filter
  const displayedFoods =
    selectedCategory === "All"
      ? foods
      : foods.filter((food) => food.strCategory === selectedCategory);

  return (
    <div className="app">
      <NavBar />

      <div className="container">
        {/* Sidebar */}
        <div className="sidebar">
          <h2>Add New Food Item</h2>
          <form onSubmit={handleAddFood} className="add-food-form">
            <input
              type="text"
              name="name"
              placeholder="Food Name"
              value={newFood.name}
              onChange={handleChange}
            />
            <input
              type="number"
              name="price"
              placeholder="Price (UGX)"
              value={newFood.price}
              onChange={handleChange}
            />
            <input
              type="text"
              name="category"
              placeholder="Category (e.g. Lunch)"
              value={newFood.category}
              onChange={handleChange}
            />
            <input
              type="text"
              name="image"
              placeholder="Image URL"
              value={newFood.image}
              onChange={handleChange}
            />
            <button type="submit">Add Food</button>
          </form>

          <div className="filter-section">
            <h2>Filter by Category</h2>
            <select
              value={selectedCategory}
              onChange={(e) => handleFilter(e.target.value)}
            >
              {categories.map((cat) => (
                <option key={cat} value={cat}>
                  {cat}
                </option>
              ))}
            </select>
          </div>
        </div>

        {/* Main Content */}
        <div className="main-content">
          <h2>Food Items</h2>
          <div className="food-grid">
            {displayedFoods.map((food) => (
              <div key={food.idMeal} className="food-card">
                <img
                  src={food.strMealThumb}
                  alt={food.strMeal}
                  className="food-image"
                />
                <h3>{food.strMeal}</h3>
                <p>{food.strCategory}</p>
                <button onClick={() => addToCart(food)}>Add to Cart</button>
              </div>
            ))}
          </div>

          {/* Cart */}
          <div className="cart-section">
            <h2> Cart</h2>
            {cart.length === 0 ? (
              <p>Your cart is empty.</p>
            ) : (
              <ul className="cart-list">
                {cart.map((item) => (
                  <li key={item.idMeal} className="cart-item">
                    <img
                      src={item.strMealThumb}
                      alt={item.strMeal}
                      className="cart-image"
                    />
                    <div className="cart-details">
                      <h4>{item.strMeal}</h4>
                      <p>Qty: {item.quantity}</p>
                      <button
                        className="remove-btn"
                        onClick={() => removeFromCart(item.idMeal)}
                      >
                        Remove
                      </button>
                    </div>
                  </li>
                ))}
              </ul>
            )}
            <h3 className="cart-total">
              Total: UGX{" "}
              {cart.reduce(
                (acc, item) => acc + (item.price || 0) * item.quantity,
                0
              )}
            </h3>
          </div>
        </div>
      </div>

      {/* Footer */}
      <footer className="footer">
        <p> 2025  Made by Mubiru Elton Felix </p>
      </footer>
    </div>
  );
}

function NavBar() {
  return (
    <div className="navbar">
      <h1>Campus Food Marketplace</h1>
    </div>
  );
}

export default App;
